body {
    margin: 0;
}

.top {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 10000000;
    font-size: 26px;
    color: white;
    background-color: rgb(27, 180, 160);
    height: 80px
}

.currentScore {
    color: white;
}
.score {
    position: relative;
    top: -100px;
    background-image: linear-gradient(to top, rgb(180, 29, 48), white);
    width: 100%;
    height: 450px;
}

.logo {
    position: relative;
    top: 175px;
    font-size: 50px;
    color: white;
}

.instructions {
    position: relative;
    top: 200px;
    font-size: 25px;
    color: white;
}
img {
    margin: 5px 0px 5px 0px;
    border: 2px solid black;
    border-radius: 10px;
}

img:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
}
